import axios from './axios'
import url from './url'

// axios.defaults.withCredentials = true

// 测试get请求
export const testGet = (load, params) => {
  return axios.get(load, params)
}

// 登录
export const login = (params) => {
  return axios.post(url.Login, params)
}

// 注册
export const register = (params) => {
  return axios.post(url.Register, params)
}

// 检查设备是否被绑定
export const checkDevice = (params) => {
  return axios.post(url.CheckDevice, params)
}

// 添加用户角色
export const saveUser = (params) => {
  return axios.post(url.SaveUserGroup, params)
}

// 添加设备到指定的角色中
export const addDevice = (params) => {
  return axios.post(url.AddDeviceToUserGroup, params)
}

// 解绑设备
export const removeDevice = (params) => {
  return axios.post(url.RemoveDevice, params)
}

// 设备最新位置和设备基本信息
export const getLastDevice = (params) => {
  return axios.post(url.PersonDeviceList, params)
}

// 设备历史轨迹
export const getDeviceHistory = (params) => {
  return axios.post(url.History, params)
}

// 健康数据
export const getHealthData = (params) => {
  return axios.post(url.Health, params)
}

// 指令下发
export const sendCommand = (params) => {
  return axios.post(url.SendCommand, params)
}

// 设备指令列表
export const getCommandList = (params) => {
  return axios.post(url.CommandList, params)
}

// 历史报警信息
export const getHistoryExcdep = (params) => {
  return axios.post(url.ExcdeptionListWhitoutCode, params)
}

// 创建安全区域
export const createGeofence = (params) => {
  return axios.post(url.CreateGeofence, params)
}

// 获取安全区域列表
export const getGeofenceList = (params) => {
  return axios.post(url.GeofenceList, params)
}

// 编辑安全区域
export const editGeofence = (params) => {
  return axios.post(url.EditGeofence, params)
}

// 删除安全区域
export const deleteGeofence = (params) => {
  return axios.post(url.DeleteGeofence, params)
}

// 反地理编码
export const getAddress = (params) => {
  return axios.post(url.Address, params)
}

// 获取用户信息
export const getUserInfo = (params) => {
  return axios.post(url.UserInfo, params)
}

// 修改头像、昵称、邮箱、手机号码
export const editUserInfo = (params) => {
  return axios.post(url.EditUserInfo, params)
}

// 修改密码
export const changePassword = (params) => {
  return axios.post(url.ChangePassword, params)
}

// 设备信息的修改
export const savePersonProfile = (params) => {
  return axios.post(url.SavePersonProfile, params)
}

// 设备信息的获取
export const getPersonProfile = (params) => {
  return axios.post(url.GetPersonProfile, params)
}

// 添加智能提醒
export const setEventRemind = (params) => {
  return axios.post(url.SetEventRemind, params)
}

// 智能提醒列表获取
export const getEventRemindList = (params) => {
  return axios.post(url.EventRemindList, params)
}

// 删除智能提醒
export const deleteEventRemind = (params) => {
  return axios.post(url.DeleteEventRemind, params)
}

// 获取设备蓝牙定位信息
export const getBTInfo = (params) => {
  return axios.post(url.BTInfo, params)
}



