import axios from 'axios'
import qs from 'qs'
// import router from '@/router'
import {Message} from 'element-ui'
import store from '../store'
// axios.defaults.baseURL = url.baseURL
// axios.defaults.withCredentials = true
// 添加一个请求拦截器
axios.interceptors.request.use(config => {
 // checkParams(config.data)
  if (config.method === 'post') {
    if (config.url.indexOf('/User/Login') === -1) {
      config.data.Token = store.getters.token
    }
    config.data = qs.stringify(config.data, { indices: false })
  }
  // if (config.pathVar) {
  //   config.url = config.url + '/' + config.pathVar
  // }
  return config
}, error => {
  return Promise.reject(error)
})

// 添加一个响应拦截器
axios.interceptors.response.use(response => {
  return new Promise((resolve, reject) => {
    if (response.data) {
      if (response.config.url.indexOf('/User/Login') > -1 && response.data.AccessToken) {
      //  token = response.data.AccessToken
        store.commit('getToken', response.data.AccessToken)
        store.commit('getUserId', response.data.Item.UserId)
        localStorage.setItem('token', response.data.AccessToken)
        localStorage.setItem('UserId', response.data.Item.UserId)
      }
      resolve(response.data)
    }
  })
}, (error) => {
  console.log(error.response)
  Message.error('调用失败')
 // getIsNull(error.response.data.msg)
})

