import Vue from 'vue'
import Router from 'vue-router'
import {ADMIN} from './role'
// import HelloWorld from '@/components/HelloWorld'
const login = () => import('../components/home/login.vue')
const main = () => import('../components/home/main.vue')
const content = () => import('../components/home/content.vue')
const member = () => import('../components/member/member.vue')
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: login
    },
    {
      path: '/main',
      name: 'main',
      component: main,
      meta: {
        role: ADMIN
      },
      children: [
        {
          path: '/',
          name: 'content',
          component: content,
          children: [
            {
              path: '/',
              name: 'member',
              component: member
            }
          ]
        }
      ]
    }
  ]
})



