// 基准大小
const baseSize = 32

// 设置 rem 函数
function setRem() {
  // 当前页面宽度相对于 1366 宽的缩放比例，可根据自己需要修改。
  const clinent = document.documentElement.clientWidth
  if (clinent > 1366) {
    const scale = clinent / 1366
    // 设置页面根节点字体大小
    document.documentElement.style.fontSize = (baseSize * Math.min(scale, 1)) + 'px'
  } else {
    const scale = 1
    // 设置页面根节点字体大小
    document.documentElement.style.fontSize = (baseSize * Math.min(scale, 1)) + 'px'
  }
}

// 初始化
setRem()
// 改变窗口大小时重新设置 rem
window.onresize = function() {
  setRem()
}



